  <MainLayout>
    <div class="assessment-summary-page">
      <div class="assessment-summary-modal">
       
        <div class="summary-cards">
          <div class="summary-card">
            <div class="summary-label">Total Sent Assessment</div>
            <div class="summary-value">7</div>
          </div>
          <div class="summary-card">
            <div class="summary-label">Submitted Assessment</div>
            <div class="summary-value">5</div>
          </div>
          <div class="summary-card">
            <div class="summary-label">Pending</div>
            <div class="summary-value">2</div>
          </div>
        </div>
        <table class="summary-table">
          <thead>
            <tr>
              <th>Member Name</th>
              <th>Result</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="row in paginatedRows" :key="row.name">
              <td>{{ row.name }}</td>
              <td>
                <span v-if="row.result === 'Submitted'" class="status submitted">
                  <svg width="20" height="20" viewBox="0 0 20 20" fill="none"><circle cx="10" cy="10" r="10" fill="#48B02C"/><path d="M6 10.5L9 13.5L14 8.5" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/></svg>
                  Submitted
                </span>
              
              </td>
              <td>
                <button class="view-btn" @click="openModal(row)">
                  <img src="@/assets/images/Notes.svg" alt="View" width="18" height="18" />
                  View
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <Pagination
          :page-size="pageSize"
          :page-sizes="pageSizes"
          :show-page-dropdown="showPageDropdown"
          :current-page="currentPage"
          :total-pages="totalPages"
          @goToPage="goToPage"
          @selectPageSize="selectPageSize"
          @togglePageDropdown="togglePageDropdown"
        />

        <!-- Modal for assessment details -->
        <div v-if="showModal" class="assessment-modal-overlay">
          <div class="assessment-modal-content">
            <button class="modal-close-btn" @click="closeModal">&times;</button>
            <h2>{{ selectedMember.name }}â€™s Assessments</h2>
            <div v-for="(q, idx) in selectedMember.assessment || []" :key="idx" class="assessment-question-block">
              <div class="assessment-question">Q.{{ idx + 1 }} {{ q.question }}</div>
              <div class="assessment-answer">{{ q.answer }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </MainLayout>
</template>

<script>
import MainLayout from '@/components/layout/MainLayout.vue';
import Pagination from '@/components/layout/Pagination.vue';
import { ref, computed } from 'vue';
import { useRoute } from 'vue-router';

export default {
  name: 'AssessmentSummaryPage',
  components: { MainLayout, Pagination },
  setup() {
    const route = useRoute();
    const assessmentId = route.params.assessmentId;

    // Example assessment data for each member
    const rows = [
      {
        name: 'Emily Carter',
        result: 'Submitted',
        assessment: [
          { question: 'What is the primary goal of a for-profit business?', answer: 'The primary goal is to maximize profits while providing value to customers.' },
          { question: 'What does SWOT stand for in business analysis?', answer: 'SWOT stands for Strengths, Weaknesses, Opportunities, and Threats.' },
          { question: 'What is the difference between leadership and management?', answer: 'Leadership focuses on inspiring and guiding people toward a vision, while management is about organizing, planning, and ensuring tasks are completed efficiently.' },
          { question: 'What is a mission statement?', answer: 'A mission statement is a brief statement that defines an organization\'s purpose, core values, and overall goals.' },
          { question: 'What is the 80/20 rule (Pareto Principle) in business?', answer: 'The 80/20 rule states that 80% of results come from 20% of efforts or customers. It helps businesses focus on the most impactful activities.' },
          { question: 'What is the difference between revenue and profit?', answer: 'Revenue is the total income a business generates from sales, while profit is what remains after deducting expenses from revenue.' }
        ]
      },
      // ...other members, add assessment data as needed...
      { name: 'James Parker', result: 'Submitted', assessment: [] },
      { name: 'Sophia Mitchell', result: 'Submitted', assessment: [] },
      { name: 'Mason Walker', result: 'Submitted', assessment: [] },
      { name: 'Olivia Bennett', result: 'Submitted', assessment: [] },
      { name: 'Benjamin Hayes', result: 'Submitted', assessment: [] },
      { name: 'Ava Richardson', result: 'Submitted', assessment: [] }
    ];

    // Pagination state
    const pageSizes = [10, 20, 50];
    const pageSize = ref(10);
    const currentPage = ref(1);
    const showPageDropdown = ref(false);

    const totalPages = computed(() =>
      Math.max(1, Math.ceil(rows.length / pageSize.value))
    );

    const paginatedRows = computed(() => {
      const start = (currentPage.value - 1) * pageSize.value;
      return rows.slice(start, start + pageSize.value);
    });

    // Modal state
    const showModal = ref(false);
    const selectedMember = ref({});

    function openModal(row) {
      selectedMember.value = row;
      showModal.value = true;
    }
    function closeModal() {
      showModal.value = false;
    }

    function goToPage(page) {
      if (page >= 1 && page <= totalPages.value) {
        currentPage.value = page;
      }
    }
    function selectPageSize(size) {
      pageSize.value = size;
      currentPage.value = 1;
      showPageDropdown.value = false;
    }
    function togglePageDropdown() {
      showPageDropdown.value = !showPageDropdown.value;
    }

    return {
      assessmentId,
      rows,
      paginatedRows,
      pageSizes,
      pageSize,
      currentPage,
      totalPages,
      showPageDropdown,
      goToPage,
      selectPageSize,
      togglePageDropdown,
      showModal,
      selectedMember,
      openModal,
      closeModal
    };
  }
};
</script>

